<script>

import gql from 'graphql-tag'
import { useQuery } from '@vue/apollo-composable'



export default {
  data() {

    const CHARACTERS_QUERY = gql`
      query login {
        login(username:"dagserver",pwdhash:"dagserver")
      }
    `
    const { result, loading, error } = useQuery(CHARACTERS_QUERY);
    return {
      result,
      loading, 
      error
    }
  },
  methods: {
    async cargarDatos() {
      return new Promise((resolve)=>{
        if(this.loading){
          let interval = setInterval(()=>{
            if(!this.loading){
              clearInterval(interval)
              resolve(this.result)
            }
          },1)
        } else {
          resolve(this.result)
        }
      });
    }
  },
  async mounted() {
    console.log(await this.cargarDatos());
  }
};
</script>

